* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}
/* banner conf para que no se reita y se vea bien  */
.banner {
  width: 100%;
  height: 100vh;
  background: url("/src/img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-attachment: fixed;
}

.banner__content {
  display: flex;
  align-items: center;
  height: 90vh;
  padding-left: 150px;
  color: whitesmoke;
}
/* maximo ancho de la pagina*/
.container {
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
}

/* limita el ancho de la caja*/
.banner__text {
  width: 500px;
}

.banner__text h3 {
  font-size: 40px;
}

.banner__text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 900px;
  margin-top: 15px;
}

.banner__text p {
  font-size: 20px;
  font-weight: 800;
  margin-top: 20px;
}

/* le da los margenes junto al navbar*/
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.navbar__text {
  display: flex;
  height: 60px;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}
.logo {
  width: 40%;
  padding-left: 120px;
}

.logo img {
  max-width: 220px;
}

/* se le quitan los estilos de ul*/
.navbar__ul {
  width: 60%;
  list-style-type: none;
  display: flex;
  align-items: flex-end;
  padding-left: 180px;
}

.navbar__ul a {
  text-decoration: none;
  color: #fff;
  padding: 0 15px;
  font-weight: 600;
  text-transform: uppercase;
}
.banner__btn {
  margin-top: 35px;
}

.btn {
  text-decoration: none;
  border: none;
  outline: none;
  color: whitesmoke;
  font-weight: 600;
  border-radius: 30px;
  font-size: 12px;
  padding: 20px 40px;
}
.btn-smart {
  background: rgb(255, 69, 79);
}
.about {
  padding: 80px 0;
}
.p-25 {
  padding: 100px;
}

/*el flex pone la imagen o el contenido dentro de la caja */
.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.col-6 {
  width: 50%;
}

.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33333333333333%;
}

.about {
  margin-bottom: 70px;
}
.about h3 {
  color: rgba(37, 37, 37, 0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}
.about h1 {
  font-size: 40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}
.about p {
  font-size: 15px;
  color: #a39c9c;
  font-weight: 500;
}
.about__btn {
  margin-top: 30px;
}

/* contener la imagen en la caja*/
.about__img {
  width: 100%;
  overflow: hidden;
}
/*imagen ajustada */
.about__img img {
  width: 100%;
  padding-right: 120px;
  margin-top: 75px;
}

.shop {
  width: 100%;
  background: url(/src/img/bg.jpg);
  height: 450px;
  background-attachment: fixed;
}
.shop-text {
  height: 450px;
  display: flex;
  align-items: center;
  width: 400px;
  justify-content: center;
  margin: 0 auto;
  color: whitesmoke;
}
.shop__text h1 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 600px;
  font-style: normal;
  letter-spacing: 0.5px;
}
.shop__btn {
  margin-top: 35px;
}
.about__img1 img {
  width: 100%;
  padding-left: 100px;
  margin-top: 75px;
}

.clients {
  width: 100%;
  padding: 100px 0;
  background: url(../src/img/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: whitesmoke;
  background-attachment: fixed;
}

.clients {
  text-align: center;
}

.client h1 {
  font-size: 45px;
}

.client p {
  font-style: 18px;
  font-weight: 400;
  margin-top: 10px;
}

.prices {
  width: 100%;
  padding: 60px 0;
}
.price {
  padding: 20px;
  text-align: center;
}
.price__img {
  width: 100%;
}
.price__img img {
  width: 250px;
}
.price__heading {
  margin-top: 20px;
  font-size: 30px;
}
.price__text {
  margin-top: 15px;
  color: #3f3d3d;
  font-size: 18px;
  font-weight: 400;
}
.price__rs {
  font-size: 25px;
  margin-top: 15px;
}

@media (max-width: 700px) {
  .navbar__text {
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
    padding-right: 850px;
  }
  .logo {
    margin-bottom: 20px;
    padding-left: 450px;
  }
  .container {
    width: 100%;
    padding: 0 30px;
  }
  .banner__text {
    width: auto;
  }

  .banner__text h3 {
    font-size: 25px !important;
    margin-top: 95px;
  }

  .banner__text h1 {
    font-size: 50px;
    line-height: 60px;
    font-weight: 700;
    margin-top: 15px;
  }
  .banner__text p {
    font-size: 20px;
    font-weight: 400;
    margin-top: 15px;
  }

  .banner__text p {
    font-size: 20px;
    font-weight: 800;
    margin-top: 20px;
    justify-content: center;
  }

  .navbar__ul li a {
    text-decoration: none;
    color: #fff;
    padding: 0 10px;
    font-weight: 300;
    text-transform: uppercase;
  }

  .col-3,
  .col-4,
  .col-6 {
    width: 100%;
  }
  .col-6 {
    padding-left: 50px;
  }
  .about__img {
    justify-content: center;
  }
  .about h1 {
    font-size: 25px;
  }
  .shop h1 {
    font-size: 255;
  }
  .btn btn-smart {
    padding-left: 50px;
  }
  .client {
    margin: 20px 0;
  }
}
